		{
			"title": "Leaning on the everlasting arms",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "35",
			"content": "<p>What a fellowship, what a joy divine,\nLeaning on the everlasting arms;\nWhat a blessedness, what a peace is mine,\nLeaning on the everlasting arms.</p>\n<p>Refrain:\nLeaning, leaning, safe and secure from all alarms;\nLeaning, leaning, leaning on the everlasting arms.</p>\n<p>O how sweet to walk in this pilgrim way,\nLeaning on the everlasting arms;\nO how bright the path grows from day to day,\nLeaning on the everlasting arms.</p>\n<p>Refrain:\nLeaning, leaning, safe and secure from all alarms;\nLeaning, leaning, leaning on the everlasting arms.</p>\n<p>What have I to dread, what have I to fear,\nLeaning on the everlasting arms;\nI have blessed peace with my Lord so near,\nLeaning on the everlasting arms.</p>\n<p>Refrain:\nLeaning, leaning, safe and secure from all alarms;\nLeaning, leaning, leaning on the everlasting arms.</p>\n",
			"slug": "leaning-on-the-everlasting-arms"
		},
		{
			"title": "When the roll is called up yonder",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "36",
			"content": "<p>When the trumpet of the Lord shall sound, and time shall be no more,\nAnd the morning breaks, eternal, bright and fair;\nWhen the saved of earth shall gather over on the other shore,\nAnd the roll is called up yonder, I’ll be there.</p>\n<p>Refrain:\nWhen the roll, is called up yonder,\nWhen the roll, is called up yonder,\nWhen the roll, is called up yonder,\nWhen the roll is called up yonder I’ll be there.</p>\n<p>On that bright and cloudless morning when the dead in Christ shall rise,\nAnd the glory of His resurrection share;\nWhen His chosen ones shall gather to their home beyond the skies,\nAnd the roll is called up yonder, I’ll be there.</p>\n<p>Refrain:\nWhen the roll, is called up yonder,\nWhen the roll, is called up yonder,\nWhen the roll, is called up yonder,\nWhen the roll is called up yonder I’ll be there.</p>\n<p>Let us labor for the Master from the dawn till setting sun,\nLet us talk of all His wondrous love and care;\nThen when all of life is over, and our work on earth is done,\nAnd the roll is called up yonder, I’ll be there.</p>\n<p>Refrain:\nWhen the roll, is called up yonder,\nWhen the roll, is called up yonder,\nWhen the roll, is called up yonder,\nWhen the roll is called up yonder I’ll be there.</p>\n",
			"slug": "when-the-roll-is-called-up-yonder"
		},
		{
			"title": "Down at the cross",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "37",
			"content": "<p>Down at the cross where my Savior died,\nDown where for cleansing from sin I cried,\nThere to my heart was the blood applied;\nGlory to His name!</p>\n<p>Refrain:\nGlory to His name, glory to His name:\nThere to my heart was the blood applied;\nGlory to His name!</p>\n<p>I am so wondrously saved from sin,\nJesus so sweetly abides within;\nThere at the cross where He took me in;\nGlory to His name!</p>\n<p>Refrain:\nGlory to His name, glory to His name:\nThere to my heart was the blood applied;\nGlory to His name!</p>\n<p>Oh, precious fountain that saves from sin,\nI am so glad I have entered in;\nThere Jesus saves me and keeps me clean;\nGlory to His name!</p>\n<p>Refrain:\nGlory to His name, glory to His name:\nThere to my heart was the blood applied;\nGlory to His name!</p>\n<p>Come to this fountain so rich and sweet,\nCast thy poor soul at the Savior’s feet;\nPlunge in today, and be made complete;\nGlory to His name!</p>\n<p>Refrain:\nGlory to His name, glory to His name:\nThere to my heart was the blood applied;\nGlory to His name!</p>\n",
			"slug": "down-at-the-cross"
		},
		{
			"title": "Love lifted me",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "38",
			"content": "<p>I was sinking deep in sin, far from the peaceful shore,\nVery deeply stained within, sinking to rise no more,\nBut the Master of the sea, heard my despairing cry,\nFrom the waters lifted me, now safe am I.</p>\n<p>Refrain:\nLove lifted me! Love lifted me!\nWhen nothing else could help\nLove lifted me!\nLove lifted me! Love lifted me!\nWhen nothing else could help\nLove lifted me!</p>\n<p>All my heart to Him I give, ever to Him I’ll cling\nIn His blessèd presence live, ever His praises sing,\nLove so mighty and so true, merits my soul’s best songs,\nFaithful, loving service too, to Him belongs.</p>\n<p>Refrain:\nLove lifted me! Love lifted me!\nWhen nothing else could help\nLove lifted me!\nLove lifted me! Love lifted me!\nWhen nothing else could help\nLove lifted me!</p>\n<p>Souls in danger look above, Jesus completely saves,\nHe will lift you by His love, out of the angry waves.\nHe’s the Master of the sea, billows His will obey,\nHe your Savior wants to be, be saved today.</p>\n<p>Refrain:\nLove lifted me! Love lifted me!\nWhen nothing else could help\nLove lifted me!\nLove lifted me! Love lifted me!\nWhen nothing else could help\nLove lifted me!</p>\n",
			"slug": "love-lifted-me"
		},
		{
			"title": "There is a fountain",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "39",
			"content": "<p>There is a fountain filled with blood drawn from Emmanuel’s veins;\nAnd sinners plunged beneath that flood lose all their guilty stains.\nLose all their guilty stains, lose all their guilty stains;\nAnd sinners plunged beneath that flood lose all their guilty stains.</p>\n<p>The dying thief rejoiced to see that fountain in his day;\nAnd there have I, though vile as he, washed all my sins away.\nWashed all my sins away, washed all my sins away;\nAnd there have I, though vile as he, washed all my sins away.</p>\n<p>E’er since, by faith, I saw the stream Thy flowing wounds supply,\nRedeeming love has been my theme, and shall be till I die.\nAnd shall be till I die, and shall be till I die;\nRedeeming love has been my theme, and shall be till I die.</p>\n<p>When this poor lisping, stammering tongue lies silent in the grave.\nThen in a nobler, sweeter song, I’ll sing Thy power to save,\nI’ll sing Thy power to save, I’ll sing Thy power to save;\nThen in a nobler, sweeter song, I’ll sing Thy power to save.</p>\n",
			"slug": "there-is-a-fountain"
		},
		{
			"title": "Redeemed",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "40",
			"content": "<p>Redeemed, how I love to proclaim it!\nRedeemed by the blood of the Lamb;\nRedeemed through His infinite mercy,\nHis child and forever I am.</p>\n<p>Refrain:\nRedeemed, redeemed,\nRedeemed by the blood of the Lamb;\nRedeemed, redeemed,\nHis child and forever I am.</p>\n<p>Redeemed, and so happy in Jesus,\nNo language my rapture can tell;\nI know that the light of His presence\nWith me doth continually dwell.</p>\n<p>Refrain:\nRedeemed, redeemed,\nRedeemed by the blood of the Lamb;\nRedeemed, redeemed,\nHis child and forever I am.</p>\n<p>I think of my blessèd Redeemer,\nI think of Him all the day long:\nI sing, for I cannot be silent;\nHis love is the theme of my song.</p>\n<p>Refrain:\nRedeemed, redeemed,\nRedeemed by the blood of the Lamb;\nRedeemed, redeemed,\nHis child and forever I am.</p>\n<p>I know I shall see in His beauty\nThe King in whose law I delight;\nWho lovingly guardeth my footsteps,\nAnd giveth me songs in the night.</p>\n<p>Refrain:\nRedeemed, redeemed,\nRedeemed by the blood of the Lamb;\nRedeemed, redeemed,\nHis child and forever I am.</p>\n",
			"slug": "redeemed"
		},
		{
			"title": "Standing on the Promises",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "41",
			"content": "<p>Standing on the promises of Christ my king,\nThrough eternal ages let His praises ring,\nGlory in the highest, I will shout and sing,\nStanding on the promises of God.</p>\n<p>Refrain:\nStanding, standing,\nStanding on the promises of God my Savior;\nStanding, standing,\nI’m standing on the promises of God.</p>\n<p>Standing on the promises that cannot fail,\nWhen the howling storms of doubt and fear assail,\nBy the living Word of God I shall prevail,\nStanding on the promises of God.</p>\n<p>Refrain:\nStanding, standing,\nStanding on the promises of God my Savior;\nStanding, standing,\nI’m standing on the promises of God.</p>\n<p>Standing on the promises of Christ the Lord,\nBound to Him eternally by love’s strong cord,\nOvercoming daily with the Spirit’s sword,\nStanding on the promises of God.</p>\n<p>Refrain:\nStanding, standing,\nStanding on the promises of God my Savior;\nStanding, standing,\nI’m standing on the promises of God.</p>\n<p>Standing on the promises I cannot fall,\nListening every moment to the Spirit’s call\nResting in my Savior as my all in all,\nStanding on the promises of God.</p>\n<p>Refrain:\nStanding, standing,\nStanding on the promises of God my Savior;\nStanding, standing,\nI’m standing on the promises of God.</p>\n",
			"slug": "standing-on-the-promises"
		},
		{
			"title": "Trust and obey",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "42",
			"content": "<p>When we walk with the Lord in the light of His Word,\nWhat a glory He sheds on our way!\nWhile we do His good will, He abides with us still,\nAnd with all who will trust and obey.</p>\n<p>Refrain:\nTrust and obey, for there’s no other way\nTo be happy in Jesus, but to trust and obey.</p>\n<p>Not a burden we bear, not a sorrow we share,\nBut our toil He doth richly repay;\nNot a grief or a loss, not a frown or a cross,\nBut is blessed if we trust and obey.</p>\n<p>Refrain:\nTrust and obey, for there’s no other way\nTo be happy in Jesus, but to trust and obey.</p>\n<p>But we never can prove the delights of His love\nUntil all on the altar we lay;\nFor the favor He shows, and the joy He bestows,\nAre for them who will trust and obey.</p>\n<p>Refrain:\nTrust and obey, for there’s no other way\nTo be happy in Jesus, but to trust and obey.</p>\n<p>Then in fellowship sweet we will sit at His feet.\nOr we’ll walk by His side in the way.\nWhat He says we will do, where He sends we will go;\nNever fear, only trust and obey.</p>\n<p>Refrain:\nTrust and obey, for there’s no other way\nTo be happy in Jesus, but to trust and obey.</p>\n",
			"slug": "trust-and-obey"
		},
		{
			"title": "Give me oil in my lamp",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "43",
			"content": "<p>Give me oil in my lamp, keep me burning.\nGive me oil in my lamp, I pray.\nGive me oil in my lamp, keep me burning.\nKeep me burning till the break of day.</p>\n<p>Refrain:\nSing hosanna, sing hosanna,\nsing hosanna to the King of kings!\nSing hosanna, sing hosanna,\nsing hosanna to the King!</p>\n<p>Make me a fisher of men, keep me seeking.\nMake me a fisher of men, I pray.\nMake me a fisher of men, keep me seeking.\nSeeking souls till Jesus comes again.</p>\n<p>Refrain:\nSing hosanna, sing hosanna,\nsing hosanna to the King of kings!\nSing hosanna, sing hosanna,\nsing hosanna to the King!</p>\n<p>Give me joy in my heart, keep me praising.\nGive me joy in my heart, I pray.\nGive me joy in my heart, keep me praising.\nKeep me praising till the break of day.</p>\n<p>Refrain:\nSing hosanna, sing hosanna,\nsing hosanna to the King of kings!\nSing hosanna, sing hosanna,\nsing hosanna to the King!</p>\n<p>Give me love in my heart, keep me serving.\nGive me love in my heart, I pray.\nGive me love in my heart, keep me serving.\nKeep me serving till the break of day.</p>\n<p>Refrain:\nSing hosanna, sing hosanna,\nsing hosanna to the King of kings!\nSing hosanna, sing hosanna,\nsing hosanna to the King!</p>\n",
			"slug": "give-me-oil-in-my-lamp"
		},
		{
			"title": "Seek ye first",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "45",
			"content": "<p>Seek ye first the kingdom of God\nAnd His righteousness;\nAnd all these things shall be added unto you.\nHallelu, Hallelujah!</p>\n<p>Man shall not live by bread alone,\nBut by every word\nThat proceeds from the mouth of God.\nHallelu, Hallelujah!</p>\n<p>Ask, and it shall be given unto you;\nSeek, and ye shall find.\nKnock, and the door shall be opened unto you.\nHallelu, Hallelujah!</p>\n",
			"slug": "seek-ye-first"
		},
		{
			"title": "Sanctuary",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "45A",
			"content": "<p>Lord, prepare me to be a sanctuary,\nPure and holy, tried and true.\nWith thanksgiving, I’ll be a living\nSanctuary for You.</p>\n",
			"slug": "sanctuary"
		},
		{
			"title": "Jesus loves the little children",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "45B",
			"content": "<p>Jesus loves the little children\nAll the children of the world.\nRed and yellow, black and white,\nthey are precious in his sight.\nJesus loves the little children of the world.</p>\n",
			"slug": "jesus-loves-the-little-children"
		},
		{
			"title": "I love to tell the story",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "46",
			"content": "<p>I love to tell the story of unseen things above,\nOf Jesus and His glory, of Jesus and His love.\nI love to tell the story, because I know ’tis true;\nIt satisfies my longings as nothing else can do.</p>\n<p>Refrain:\nI love to tell the story, ’twill be my theme in glory,\nTo tell the old, old story of Jesus and His love.</p>\n<p>I love to tell the story; ’tis pleasant to repeat\nWhat seems, each time I tell it, more wonderfully sweet.\nI love to tell the story, for some have never heard\nThe message of salvation from God’s own holy Word.</p>\n<p>Refrain:\nI love to tell the story, ’twill be my theme in glory,\nTo tell the old, old story of Jesus and His love.</p>\n<p>I love to tell the story, for those who know it best\nSeem hungering and thirsting to hear it like the rest.\nAnd when, in scenes of glory, I sing the new, new song,\n’Twill be the old, old story that I have loved so long.</p>\n<p>Refrain:\nI love to tell the story, ’twill be my theme in glory,\nTo tell the old, old story of Jesus and His love.</p>\n",
			"slug": "i-love-to-tell-the-story"
		},
		{
			"title": "Imortal, invisible",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "47",
			"content": "<p>Immortal, invisible, God only wise,\nIn light inaccessible hid from our eyes,\nMost blessèd, most glorious, the Ancient of Days,\nAlmighty, victorious, Thy great name we praise.</p>\n<p>Unresting, unhasting, and silent as light,\nNor wanting, nor wasting, Thou rulest in might;\nThy justice, like mountains, high soaring above\nThy clouds, which are fountains of goodness and love.</p>\n<p>To all, life Thou givest, to both great and small;\nIn all life Thou livest, the true life of all;\nWe blossom and flourish as leaves on the tree,\nAnd wither and perish—but naught changeth Thee.</p>\n<p>Great Father of glory, pure Father of light,\nThine angels adore Thee, all veiling their sight;\nAll praise we would render; oh help us to see\n’Tis only the spendor of light hideth Thee.</p>\n",
			"slug": "imortal-invisible"
		},
		{
			"title": "Come just as you are",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "48",
			"content": "<p>You who are troubled and burdened by sin,\nCome just as you are;\nCome to the Savior, a new life begin,\nOh, come just as you are.</p>\n<p>Refrain:\nCome just as you are,\nOh, come just as you are;\nTurn from your sin, let the Savior come in,\nAnd come just as you are.</p>\n<p>Deep in your heart sin has written its scar,\nCome just as you are;\nThough from your Father you’ve wandered afar,\nOh, come just as you are.</p>\n<p>Refrain:\nCome just as you are,\nOh, come just as you are;\nTurn from your sin, let the Savior come in,\nAnd come just as you are.</p>\n<p>Sinful and guilty, heart broken and lost,\nCome just as you are;\nThink what your ransom on Calvary cost!\nOh, come just as you are.</p>\n<p>Refrain:\nCome just as you are,\nOh, come just as you are;\nTurn from your sin, let the Savior come in,\nAnd come just as you are.</p>\n<p>Naught with your goodness for sin can atone,\nCome just as you are;\nTrust in the merit of Jesus alone,\nAnd come just as you are.</p>\n<p>Refrain:\nCome just as you are,\nOh, come just as you are;\nTurn from your sin, let the Savior come in,\nAnd come just as you are.</p>\n<p>Come with your heartaches, your sorrow and pain,\nCome just as you are;\nNo one has come to the Savior in vain,\nOh, come just as you are.</p>\n<p>Refrain:\nCome just as you are,\nOh, come just as you are;\nTurn from your sin, let the Savior come in,\nAnd come just as you are.</p>\n",
			"slug": "come-just-as-you-are"
		},
		{
			"title": "He keeps me singing",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "49",
			"content": "<p>There’s within my heart a melody\nJesus whispers sweet and low,\nFear not, I am with thee, peace, be still,\nIn all of life’s ebb and flow.</p>\n<p>Refrain:\nJesus, Jesus, Jesus,\nSweetest name I know,\nFills my every longing,\nKeeps me singing as I go.</p>\n<p>All my life was wrecked by sin and strife,\nDiscord filled my heart with pain,\nJesus swept across the broken strings,\nStirred the slumbering chords again.</p>\n<p>Refrain:\nJesus, Jesus, Jesus,\nSweetest name I know,\nFills my every longing,\nKeeps me singing as I go.</p>\n<p>Feasting on the riches of His grace,\nResting ’neath His sheltering wing,\nAlways looking on His smiling face,\nThat is why I shout and sing.</p>\n<p>Refrain:\nJesus, Jesus, Jesus,\nSweetest name I know,\nFills my every longing,\nKeeps me singing as I go.</p>\n<p>Though sometimes He leads through waters deep,\nTrials fall across the way,\nThough sometimes the path seems rough and steep,\nSee His footprints all the way.</p>\n<p>Refrain:\nJesus, Jesus, Jesus,\nSweetest name I know,\nFills my every longing,\nKeeps me singing as I go.</p>\n<p>Soon He’s coming back to welcome me,\nFar beyond the starry sky;\nI shall wing my flight to worlds unknown,\nI shall reign with Him on high.</p>\n<p>Refrain:\nJesus, Jesus, Jesus,\nSweetest name I know,\nFills my every longing,\nKeeps me singing as I go.</p>\n",
			"slug": "he-keeps-me-singing"
		},
		{
			"title": "Send the light",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "50",
			"content": "<p>There’s a call comes ringing over the restless wave,\n&quot;Send the light! Send the light!&quot;\nThere are souls to rescue, there are souls to save,\nSend the light! Send the light!</p>\n<p>Refrain:\nSend the light, the blessèd Gospel light;\nLet it shine from shore to shore!\nSend the light, the blessèd Gospel light;\nLet it shine forevermore!</p>\n<p>We have heard the Macedonian call today,\n&quot;Send the light! Send the light!&quot;\nAnd a golden offering at the cross we lay,\nSend the light! Send the light!</p>\n<p>Refrain:\nSend the light, the blessèd Gospel light;\nLet it shine from shore to shore!\nSend the light, the blessèd Gospel light;\nLet it shine forevermore!</p>\n<p>Let us pray that grace may everywhere abound,\n&quot;Send the light! Send the light!&quot;\nAnd a Christlike spirit everywhere be found,\nSend the light! Send the light!</p>\n<p>Refrain:\nSend the light, the blessèd Gospel light;\nLet it shine from shore to shore!\nSend the light, the blessèd Gospel light;\nLet it shine forevermore!</p>\n<p>Let us not grow weary in the work of love,\n&quot;Send the light! Send the light!&quot;\nLet us gather jewels for a crown above,\nSend the light! Send the light!</p>\n<p>Refrain:\nSend the light, the blessèd Gospel light;\nLet it shine from shore to shore!\nSend the light, the blessèd Gospel light;\nLet it shine forevermore!</p>\n",
			"slug": "send-the-light"
		},
		{
			"title": "Count your blessings",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "51",
			"content": "<p>When upon life’s billows you are tempest tossed,\nWhen you are discouraged, thinking all is lost,\nCount your many blessings, name them one by one,\nAnd it will surprise you what the Lord hath done.</p>\n<p>Refrain:\nCount your blessings, name them one by one,\nCount your blessings, see what God hath done!\nCount your blessings, name them one by one,\nAnd it will surprise you what the Lord hath done.</p>\n<p>Are you ever burdened with a load of care?\nDoes the cross seem heavy you are called to bear?\nCount your many blessings, every doubt will fly,\nAnd you will keep singing as the days go by.</p>\n<p>Refrain:\nCount your blessings, name them one by one,\nCount your blessings, see what God hath done!\nCount your blessings, name them one by one,\nAnd it will surprise you what the Lord hath done.</p>\n<p>When you look at others with their lands and gold,\nThink that Christ has promised you His wealth untold;\nCount your many blessings. Money can not buy\nYour reward in heaven, nor your home on high.</p>\n<p>Refrain:\nCount your blessings, name them one by one,\nCount your blessings, see what God hath done!\nCount your blessings, name them one by one,\nAnd it will surprise you what the Lord hath done.</p>\n<p>So, amid the conflict whether great or small,\nDo not be discouraged, God is over all;\nCount your many blessings, angels will attend,\nHelp and comfort give you to your journey’s end.</p>\n<p>Refrain:\nCount your blessings, name them one by one,\nCount your blessings, see what God hath done!\nCount your blessings, name them one by one,\nAnd it will surprise you what the Lord hath done.</p>\n",
			"slug": "count-your-blessings"
		},
		{
			"title": "Blessed be the name",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "52",
			"content": "<p>Oh, for a thousand tongues to sing,\nBlessed be the Name of the Lord!\nThe glories of my God and King!\nBlessed be the Name of the Lord!</p>\n<p>Refrain:\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!</p>\n<p>Jesus! the Name that calms my fears,\nBlessed be the Name of the Lord!\n’Tis music in the sinner’s ears,\nBlessed be the Name of the Lord!</p>\n<p>Refrain:\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!</p>\n<p>He breaks the pow’r of canceled sin,\nBlessed be the Name of the Lord!\nHis blood can make the foulest clean,\nBlessed be the Name of the Lord!</p>\n<p>Refrain:\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!</p>\n<p>I never shall forget that day,\nBlessed be the Name of the Lord!\nWhen Jesus washed my sins away,\nBlessed be the Name of the Lord!</p>\n<p>Refrain:\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!\nBlessed be the Name, blessed be the Name,\nBlessed be the Name of the Lord!</p>\n",
			"slug": "blessed-be-the-name"
		},
		{
			"title": "Jesus paid it all",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "53",
			"content": "<p>I hear the Savior say,\n“Thy strength indeed is small;\nChild of weakness, watch and pray,\nFind in Me thine all in all.”</p>\n<p>Refrain:\nJesus paid it all,\nAll to Him I owe;\nSin had left a crimson stain,\nHe washed it white as snow.</p>\n<p>Lord, now indeed I find\nThy power and Thine alone,\nCan change the leper’s spots\nAnd melt the heart of stone.</p>\n<p>Refrain:\nJesus paid it all,\nAll to Him I owe;\nSin had left a crimson stain,\nHe washed it white as snow.</p>\n<p>For nothing good have I\nWhereby Thy grace to claim,\nI’ll wash my garments white\nIn the blood of Calv’ry’s Lamb.</p>\n<p>Refrain:\nJesus paid it all,\nAll to Him I owe;\nSin had left a crimson stain,\nHe washed it white as snow.</p>\n<p>And when before the throne\nI stand in Him complete,\n&quot;Jesus died my soul to save,&quot;\nMy lips shall still repeat.</p>\n<p>Refrain:\nJesus paid it all,\nAll to Him I owe;\nSin had left a crimson stain,\nHe washed it white as snow.</p>\n",
			"slug": "jesus-paid-it-all"
		},
		{
			"title": "At Calvary",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "54",
			"content": "<p>Years I spent in vanity and pride,\nCaring not my Lord was crucified,\nKnowing not it was for me He died on Calvary.</p>\n<p>Refrain:\nMercy there was great, and grace was free;\nPardon there was multiplied to me;\nThere my burdened soul found liberty at Calvary.</p>\n<p>By God’s Word at last my sin I learned;\nThen I trembled at the law I’d spurned,\nTill my guilty soul imploring turned to Calvary.</p>\n<p>Refrain:\nMercy there was great, and grace was free;\nPardon there was multiplied to me;\nThere my burdened soul found liberty at Calvary.</p>\n<p>Now I’ve given to Jesus everything,\nNow I gladly own Him as my King,\nNow my raptured soul can only sing of Calvary!</p>\n<p>Refrain:\nMercy there was great, and grace was free;\nPardon there was multiplied to me;\nThere my burdened soul found liberty at Calvary.</p>\n<p>Oh, the love that drew salvation’s plan!\nOh, the grace that brought it down to man!\nOh, the mighty gulf that God did span at Calvary!</p>\n<p>Refrain:\nMercy there was great, and grace was free;\nPardon there was multiplied to me;\nThere my burdened soul found liberty at Calvary.</p>\n",
			"slug": "at-calvary"
		},
		{
			"title": "Silver and Gold have I none",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "55",
			"content": "<p>Peter and John went to pray.\nThey met a lame man on the way.</p>\n<p>He asked for alms and held out his palms,\nAnd this is what Peter did say,</p>\n<p>&quot;Silver and gold have I none,\nBut such as I have give I thee,</p>\n<p>In the name of Jesus Christ of Nazareth, rise up and walk.&quot;\nHe went walking and leaping and praising God,\nWalking and leaping and praising God,\nIn the name of Jesus Christ of Nazareth, rise up and walk.</p>\n",
			"slug": "silver-and-gold-have-i-none"
		},
		{
			"title": "Jesus loves me",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "56",
			"content": "<p>Jesus loves me! This I know,\nFor the Bible tells me so;\nLittle ones to Him belong;\nThey are weak, but He is strong.</p>\n<p>Refrain:\nYes, Jesus loves me!\nYes, Jesus loves me!\nYes, Jesus loves me!\nThe Bible tells me so.</p>\n",
			"slug": "jesus-loves-me"
		},
		{
			"title": "Free from the Law, oh happy condition",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "57",
			"content": "<p>Free from the law, O happy condition,\nJesus has bled and there is remission,\nCursed by the law and bruised by the fall,\nGrace hath redeemed us once for all.</p>\n<p>Refrain:\nOnce for all, O sinner, receive it,\nOnce for all, O friend, now believe it;\nCling to the cross, the burden will fall,\nChrist hath redeemed us once for all.</p>\n<p>Now we are free, there’s no condemnation,\nJesus provides a perfect salvation.\n“Come unto Me,” O hear His sweet call,\nCome, and He saves us once for all.</p>\n<p>Refrain:\nOnce for all, O sinner, receive it,\nOnce for all, O friend, now believe it;\nCling to the cross, the burden will fall,\nChrist hath redeemed us once for all.</p>\n<p>“Children of God,” O glorious calling,\nSurely His grace will keep us from falling;\nPassing from death to life at His call;\nBlessèd salvation once for all.</p>\n<p>Refrain:\nOnce for all, O sinner, receive it,\nOnce for all, O friend, now believe it;\nCling to the cross, the burden will fall,\nChrist hath redeemed us once for all.</p>\n",
			"slug": "free-from-the-law-oh-happy-condition"
		},
		{
			"title": "This little light of mine",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "58",
			"content": "<p>This little light of mine, I’m gonna let it shine.\nThis little light of mine, I’m gonna let it shine,\nLet it shine, let it shine, let it shine.</p>\n<p>Hide it under a bushel - NO!\nI’m gonna let it shine,\nHide it under a bushel - NO!\nI’m gonna let it shine,\nLet it shine, let it shine, let it shine.</p>\n<p>Don’t let Satan blow it out.\nI’m gonna let it shine\nDon’t let Satan blow it out.\nI’m gonna let it shine,\nLet it shine, let it shine, let it shine.</p>\n<p>This little light of mine, I’m gonna let it shine.\nThis little light of mine, I’m gonna let it shine,\nLet it shine, let it shine, let it shine.</p>\n",
			"slug": "this-little-light-of-mine"
		},
		{
			"title": "I need thee ever hour",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "59",
			"content": "<p>I need Thee every hour, most gracious Lord;\nNo tender voice like Thine can peace afford.</p>\n<p>Refrain:\nI need Thee, O I need Thee;\nEvery hour I need Thee;\nO bless me now, my Savior,\nI come to Thee.</p>\n<p>I need Thee every hour, stay Thou nearby;\nTemptations lose their power when Thou art nigh.</p>\n<p>Refrain:\nI need Thee, O I need Thee;\nEvery hour I need Thee;\nO bless me now, my Savior,\nI come to Thee.</p>\n<p>I need Thee every hour, in joy or pain;\nCome quickly and abide, or life is in vain.</p>\n<p>Refrain:\nI need Thee, O I need Thee;\nEvery hour I need Thee;\nO bless me now, my Savior,\nI come to Thee.</p>\n<p>I need Thee every hour; teach me Thy will;\nAnd Thy rich promises in me fulfill.</p>\n<p>Refrain:\nI need Thee, O I need Thee;\nEvery hour I need Thee;\nO bless me now, my Savior,\nI come to Thee.</p>\n<p>I need Thee every hour, most Holy One;\nO make me Thine indeed, Thou blessèd Son.</p>\n<p>Refrain:\nI need Thee, O I need Thee;\nEvery hour I need Thee;\nO bless me now, my Savior,\nI come to Thee.</p>\n",
			"slug": "i-need-thee-ever-hour"
		},
		{
			"title": "Because he lives",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "60",
			"content": "<p>God sent his son\nThey called him Jesus\nHe came to love\nHeal and forgive\nHe lived and died\nTo buy my pardon\nAn empty grave\nIs there to prove\nMy Savior lives</p>\n<p>(Chorus)\nBecause he lives\nI can face tomorrow\nBecause he lives\nAll fear is gone\nBecause I know\nHe holds the future\nAnd life is worth the living\nJust because he lives</p>\n<p>How sweet to hold\nA newborn baby\nAnd feel the pride\nAnd joy he gives\nBut greater still\nThe calm assurance\nThis child can face\nUncertain days\nBecause he lives</p>\n<p>(Chorus)\nBecause he lives\nI can face tomorrow\nBecause he lives\nAll fear is gone\nBecause I know\nHe holds the future\nAnd life is worth the living\nJust because he lives</p>\n<p>And then one day\nI’ll cross the river\nI’ll fight life’s final war with pain\nAnd then as death\nGives way to victory\nI’ll see the lights\nOf glory and\nI’ll know he lives</p>\n<p>(Chorus)\nBecause he lives\nI can face tomorrow\nBecause he lives\nAll fear is gone\nBecause I know\nHe holds the future\nAnd life is worth the living\nJust because he lives</p>\n",
			"slug": "because-he-lives"
		},
		{
			"title": "Just like you",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "61",
			"content": "<p>Oh Lord, I want to follow only You.\nOh Lord, I want to follow only You.\nI want to follow, I want to follow.\nOh Lord, I want to follow only You.</p>\n<p>Oh Lord, give me wisdom to know what to do.\nOh Lord, give me wisdom to know what to do.\nI need Your wisdom, I need Your wisdom.\nOh Lord, give me wisdom to know what to do.</p>\n<p>Oh Lord, grant us repentance toward You.\nOh Lord, grant us repentance toward You.\nHeal our hard hearts, Lord.  Heal our hard hearts, Lord.\nOh Lord, grant us repentance toward You.</p>\n<p>Oh Lord, I want to be just like You.\nOh Lord, I want to be just like You.\nBe just like You, Lord.  Be just like You, Lord.\nOh Lord, I want to be just like You.</p>\n",
			"slug": "just-like-you"
		},
		{
			"title": "God owns Colorado and me",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "62",
			"content": "<p>Christ owns the cattle on a thousand hills,\nthat walk upon the grass between the crevices.\nHe owns my soul, He died on he cross,\nEternal life He brought us.</p>\n<p>Chorus:\nDoes He love me? Does He own me?\nYes He loves you. Yes He owns you.\nDo you love Him? Do you own Him?\nYes I love Him.  Yes I own Him.</p>\n<p>God owns the snow on top the mountain peaks,\nMelt running down the creeks and through the rivers.\nHe owns our faith, a gift from on high,\nBy which He has made us believers.</p>\n<p>Chorus:\nDoes He love me? Does He own me?\nYes He loves you. Yes He owns you.\nDo you love Him? Do you own Him?\nYes I love Him.  Yes I own Him.</p>\n<p>God owns the fragrance of the valley’s rain,\nThe scent of Aspen tree leaf and the Evergreens too.\nHe owns the fragrance this sacred life savory aroma from me and you.</p>\n<p>Chorus:\nDoes He love me? Does He own me?\nYes He loves you. Yes He owns you.\nDo you love Him? Do you own Him?\nYes I love Him.  Yes I own Him.</p>\n",
			"slug": "god-owns-colorado-and-me"
		},
		{
			"title": "He's got the whole world in his hands",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "63",
			"content": "<p>He’s got the whole world in his hands,\nHe’s got the whole world in his hands,\nHe’s got the whole world in his hands,\nHe’s got the whole world in his hands.</p>\n<p>He’s got the wind and the rain in His hands,\nHe’s got the wind and the rain in His hands,\nHe’s got the wind and the rain in His hands,\nHe’s got the whole world in his hands.</p>\n<p>He’s got the little tiny baby in his hands,\nHe’s got the little tiny baby in his hands,\nHe’s got the little tiny baby in his hands,\nHe’s got the whole world in his hands.</p>\n<p>He’s got everybody here in his hands,\nHe’s got everybody here in his hands,\nHe’s got everybody here in his hands,\nHe’s got the whole world in his hands.</p>\n",
			"slug": "he-s-got-the-whole-world-in-his-hands"
		},
		{
			"title": "As the Deer",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "64",
			"content": "<p>As the deer panteth for the water\nSo my soul longeth after thee.\nYou alone are my heart’s desire\nAnd I long to worship thee.</p>\n<p>Chorus:\nYou alone are my strength, my shield\nTo you alone may my spirit yield\nYou alone are my heart’s desire\nAnd I long to worship thee</p>\n<p>You’re my friend and you are my brother\nEven though you are a king\nI love you more than any other\nSo much more than anything</p>\n<p>Chorus:\nYou alone are my strength, my shield\nTo you alone may my spirit yield\nYou alone are my heart’s desire\nAnd I long to worship thee</p>\n<p>I want you more than gold or silver,\nonly you can satisfy.\nYou alone are the real joygiver\nand the apple of my eye.</p>\n<p>Chorus:\nYou alone are my strength, my shield\nTo you alone may my spirit yield\nYou alone are my heart’s desire\nAnd I long to worship thee</p>\n",
			"slug": "as-the-deer"
		},
		{
			"title": "I will sing of my redeemer",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "65",
			"content": "<p>I will sing of my Redeemer,\nAnd His wondrous love to me;\nOn the cruel cross He suffered,\nFrom the curse to set me free.</p>\n<p>Refrain:\nSing, oh sing, of my Redeemer,\nWith His blood, He purchased me.\nOn the cross, He sealed my pardon,\nPaid the debt, and made me free.</p>\n<p>I will tell the wondrous story,\nHow my lost estate to save,\nIn His boundless love and mercy,\nHe the ransom freely gave.</p>\n<p>Refrain:\nSing, oh sing, of my Redeemer,\nWith His blood, He purchased me.\nOn the cross, He sealed my pardon,\nPaid the debt, and made me free.</p>\n<p>I will praise my dear Redeemer,\nHis triumphant power I’ll tell,\nHow the victory He giveth\nOver sin, and death, and hell.</p>\n<p>Refrain:\nSing, oh sing, of my Redeemer,\nWith His blood, He purchased me.\nOn the cross, He sealed my pardon,\nPaid the debt, and made me free.</p>\n<p>I will sing of my Redeemer,\nAnd His heav’nly love to me;\nHe from death to life hath brought me,\nSon of God with Him to be.</p>\n<p>Refrain:\nSing, oh sing, of my Redeemer,\nWith His blood, He purchased me.\nOn the cross, He sealed my pardon,\nPaid the debt, and made me free.</p>\n",
			"slug": "i-will-sing-of-my-redeemer"
		},
		{
			"title": "We're marching to Zion",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "66",
			"content": "<p>Come, we that love the Lord,\nAnd let our joys be known;\nJoin in a song with sweet accord,\nJoin in a song with sweet accord\nAnd thus surround the throne,\nAnd thus surround the throne.</p>\n<p>Refrain:\nWe’re marching to Zion,\nBeautiful, beautiful Zion;\nWe’re marching upward to Zion,\nThe beautiful city of God.</p>\n<p>Let those refuse to sing,\nWho never knew our God;\nBut children of the heavenly King,\nBut children of the heavenly King\nMay speak their joys abroad,\nMay speak their joys abroad.</p>\n<p>Refrain:\nWe’re marching to Zion,\nBeautiful, beautiful Zion;\nWe’re marching upward to Zion,\nThe beautiful city of God.</p>\n<p>The hill of Zion yields\nA thousand sacred sweets\nBefore we reach the heav’nly fields,\nBefore we reach the heav’nly fields,\nOr walk the golden streets,\nOr walk the golden streets.</p>\n<p>Refrain:\nWe’re marching to Zion,\nBeautiful, beautiful Zion;\nWe’re marching upward to Zion,\nThe beautiful city of God.</p>\n<p>Then let our songs abound,\nAnd every tear be dry;\nWe’re marching through Immanuel’s ground,\nWe’re marching through Immanuel’s ground,\nTo fairer worlds on high,\nTo fairer worlds on high.</p>\n<p>Refrain:\nWe’re marching to Zion,\nBeautiful, beautiful Zion;\nWe’re marching upward to Zion,\nThe beautiful city of God.</p>\n",
			"slug": "we-re-marching-to-zion"
		},
		{
			"title": "Have Thine own way, Lord",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "67",
			"content": "<p>Have Thine own way, Lord! Have Thine own way!\nThou art the Potter, I am the clay.\nMold me and make me after Thy will,\nWhile I am waiting, yielded and still.</p>\n<p>Have Thine own way, Lord! Have Thine own way!\nSearch me and try me, Master, today!\nWhiter than snow, Lord, wash me just now,\nAs in Thy presence humbly I bow.</p>\n<p>Have Thine own way, Lord! Have Thine own way!\nWounded and weary, help me, I pray!\nPower, all power, surely is Thine!\nTouch me and heal me, Savior divine.</p>\n<p>Have Thine own way, Lord! Have Thine own way!\nHold o’er my being absolute sway!\nFill with Thy Spirit ’till all shall see\nChrist only, always, living in me.</p>\n",
			"slug": "have-thine-own-way-lord"
		},
		{
			"title": "Sweet by and by",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "68",
			"content": "<p>There’s a land that is fairer than day,\nAnd by faith we can see it afar;\nFor the Father waits over the way\nTo prepare us a dwelling place there.</p>\n<p>Refrain:\nIn the sweet by and by,\nWe shall meet on that beautiful shore;\nIn the sweet by and by,\nWe shall meet on that beautiful shore.</p>\n<p>We shall sing on that beautiful shore\nThe melodious songs of the blessed;\nAnd our spirits shall sorrow no more,\nNot a sigh for the blessing of rest.</p>\n<p>Refrain:\nIn the sweet by and by,\nWe shall meet on that beautiful shore;\nIn the sweet by and by,\nWe shall meet on that beautiful shore.</p>\n<p>To our bountiful Father above,\nWe will offer our tribute of praise\nFor the glorious gift of His love\nAnd the blessings that hallow our days.</p>\n<p>Refrain:\nIn the sweet by and by,\nWe shall meet on that beautiful shore;\nIn the sweet by and by,\nWe shall meet on that beautiful shore.</p>\n",
			"slug": "sweet-by-and-by"
		},
		{
			"title": "Shall we gather at the river",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "69",
			"content": "<p>Shall we gather at the river,\nWhere bright angel feet have trod,\nWith its crystal tide forever\nFlowing by the throne of God?</p>\n<p>Refrain:\nYes, we’ll gather at the river,\nThe beautiful, the beautiful river;\nGather with the saints at the river\nThat flows by the throne of God.</p>\n<p>On the margin of the river,\nWashing up its silver spray,\nWe will walk and worship ever,\nAll the happy golden day.</p>\n<p>Refrain:\nYes, we’ll gather at the river,\nThe beautiful, the beautiful river;\nGather with the saints at the river\nThat flows by the throne of God.</p>\n<p>Ere we reach the shining river,\nLay we every burden down;\nGrace our spirits will deliver,\nAnd provide a robe and crown.</p>\n<p>Refrain:\nYes, we’ll gather at the river,\nThe beautiful, the beautiful river;\nGather with the saints at the river\nThat flows by the throne of God.</p>\n<p>Soon we’ll reach the shining river,\nSoon our pilgrimage will cease;\nSoon our happy hearts will quiver\nWith the melody of peace.</p>\n<p>Refrain:\nYes, we’ll gather at the river,\nThe beautiful, the beautiful river;\nGather with the saints at the river\nThat flows by the throne of God.</p>\n",
			"slug": "shall-we-gather-at-the-river"
		},
		{
			"title": "Righteous Concern",
			"date": "2015-05-05T18:40:02.000Z",
			"song": "70",
			"content": "<p>Verse 1\nGod seems depraved and indifferent to allow all this pain.\nDo you question how can it be?\nIsn’t this the question caused men’s faith to fail because they could not see.</p>\n<p>Chorus\nGod has a righteous concern a righteous concern!\nHe’s not indifferent a million miles away.\nHe has a righteous concern a righteous concern.\nOnly trust Him ­ it’s all for your good.</p>\n<p>Verse 2\nAre you suffering today? Does He seem far away? Are you tempted to give up on Him?\nIs your faith at the brink? Is it ready to sink? Don’t lose hope! He has a righteous concern.</p>\n<p>Verse 3\nIs your marriage suffering? Is your child wayward? Do financial difficulties reign?\nAre you lonely today? Is your health fading away? Don’t lose hope!\nHe has a righteous concern.</p>\n",
			"slug": "righteous-concern"
		},
		{
			"title": "AND CAN IT BE THAT I SHOULD GAIN?",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "71",
			"content": "<p>And can it be that I should gain\nAn interest in the Savior’s blood?\nDied He for me, who caused His pain—\nFor me, who Him to death pursued?\nAmazing love! How can it be,\nThat Thou, my God, shouldst die for me?\nAmazing love! How can it be,\nThat Thou, my God, shouldst die for me?</p>\n<p>’Tis mystery all: th’Immortal dies:\nWho can explore His strange design?\nIn vain the firstborn seraph tries\nTo sound the depths of love divine.\n’Tis mercy all! Let earth adore,\nLet angel minds inquire no more.\n’Tis mercy all! Let earth adore;\nLet angel minds inquire no more.</p>\n<p>He left His Father’s throne above\nSo free, so infinite His grace—\nEmptied Himself of all but love,\nAnd bled for Adam’s helpless race:\n’Tis mercy all, immense and free,\nFor O my God, it found out me!\n’Tis mercy all, immense and free,\nFor O my God, it found out me!</p>\n<p>Long my imprisoned spirit lay,\nFast bound in sin and nature’s night;\nThine eye diffused a quickening ray—\nI woke, the dungeon flamed with light;\nMy chains fell off, my heart was free,\nI rose, went forth, and followed Thee.\nMy chains fell off, my heart was free,\nI rose, went forth, and followed Thee.</p>\n<p>Still the small inward voice I hear,\nThat whispers all my sins forgiven;\nStill the atoning blood is near,\nThat quenched the wrath of hostile Heaven.\nI feel the life His wounds impart;\nI feel the Savior in my heart.\nI feel the life His wounds impart;\nI feel the Savior in my heart.</p>\n<p>No condemnation now I dread;\nJesus, and all in Him, is mine;\nAlive in Him, my living Head,\nAnd clothed in righteousness divine,\nBold I approach th’eternal throne,\nAnd claim the crown, through Christ my own.\nBold I approach th’eternal throne,\nAnd claim the crown, through Christ my own.</p>\n",
			"slug": "and-can-it-be-that-i-should-gain"
		},
		{
			"title": "Joy to the World",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "A",
			"content": "<p>Joy to the world, the Lord is come!\nLet earth receive her king;\nLet every heart prepare Him room,\nAnd Heaven and nature sing,\nAnd Heaven and nature sing,\nAnd Heaven, and Heaven, and nature sing.</p>\n<p>Joy to the world, the Savior reigns!\nLet men their songs employ;\nWhile fields and floods, rocks, hills and plains\nRepeat the sounding joy,\nRepeat the sounding joy,\nRepeat, repeat, the sounding joy.</p>\n<p>He rules the world with truth and grace,\nAnd makes the nations prove\nThe glories of His righteousness,\nAnd wonders of His love,\nAnd wonders of His love,\nAnd wonders, wonders, of His love.</p>\n",
			"slug": "joy-to-the-world"
		},
		{
			"title": "We three Kings",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "B",
			"content": "<p>We three kings of Orient are\nBearing gifts we traverse afar.\nField and fountain, moor and mountain,\nFollowing yonder star.</p>\n<p>O star of wonder, star of night,\nStar with royal beauty bright,\nWestward leading, still proceeding,\nGuide us to thy perfect Light.</p>\n<p>Born a king on Bethlehem’s plain,\nGold I bring to crown Him again,\nKing forever, ceasing never\nOver us all to reign.</p>\n<p>O star of wonder, star of night,\nStar with royal beauty bright,\nWestward leading, still proceeding,\nGuide us to thy perfect Light.</p>\n<p>Frankincense to offer have I.\nIncense owns a Deity nigh.\nPrayer and praising all men raising,\nWorship Him, God most high.</p>\n<p>O star of wonder, star of night,\nStar with royal beauty bright,\nWestward leading, still proceeding,\nGuide us to thy perfect Light.</p>\n<p>Myrrh is mine: Its bitter perfume\nBreaths a life of gathering gloom.\nSorrowing, sighing, bleeding dying,\nSealed in the stone-cold tomb.</p>\n<p>O star of wonder, star of night,\nStar with royal beauty bright,\nWestward leading, still proceeding,\nGuide us to thy perfect Light.</p>\n<p>Glorious now behold Him arise,\nKing and God and Sacrifice.\nAlleluia, alleluia!\nEarth to heaven replies.</p>\n<p>O star of wonder, star of night,\nStar with royal beauty bright,\nWestward leading, still proceeding,\nGuide us to thy perfect Light</p>\n",
			"slug": "we-three-kings"
		},
		{
			"title": "Hark! The Herold Angels sing",
			"date": "2015-05-09T18:40:02.000Z",
			"song": "C",
			"content": "<p>Hark! The herald angels sing,\n&quot;Glory to the newborn King;\nPeace on earth, and mercy mild,\nGod and sinners reconciled!&quot;\nJoyful, all ye nations rise,\nJoin the triumph of the skies;\nWith th’angelic host proclaim,\n“Christ is born in Bethlehem!”</p>\n<p>Refrain:\nHark! the herald angels sing,\n“Glory to the newborn King!”</p>\n<p>Christ, by highest Heav’n adored;\nChrist the everlasting Lord;\nLate in time, behold Him come,\nOffspring of a virgin’s womb.\nVeiled in flesh the Godhead see;\nHail th’incarnate Deity,\nPleased as man with man to dwell,\nJesus our Emmanuel.</p>\n<p>Refrain:\nHark! the herald angels sing,\n“Glory to the newborn King!”</p>\n<p>Hail the heav’n born Prince of Peace!\nHail the Sun of Righteousness!\nLight and life to all He brings,\nRis’n with healing in His wings.\nMild He lays His glory by,\nBorn that man no more may die.\nBorn to raise the sons of earth,\nBorn to give them second birth.</p>\n<p>Refrain:\nHark! the herald angels sing,\n“Glory to the newborn King!”</p>\n",
			"slug": "hark-the-herold-angels-sing"
		}
